import { __ } from "@wordpress/i18n";
import { withSelect } from "@wordpress/data";
import { InspectorControls } from "@wordpress/block-editor";

import { ClipboardButton, TabPanel, Panel, PanelBody, PanelRow, Snackbar } from "@wordpress/components";
import { useState } from "@wordpress/element";
import ModelForm from "./Tabs/General/ModelForm";
import Style from "./Tabs/Style/Style";
import Additional from "./Tabs/Style/Additional";
import Options from "./Tabs/General/Options";
import O3DVOptions from "./Tabs/General/O3DVOptions";
import Texture from "./Tabs/General/Texture";

// import "./settings.scss";
import Lighting from './Tabs/General/Lighting';
// import AR from './Components/Tabs/General/AR';

const Settings = ({ attributes, setAttributes, postId, postType, viewerRef, modelReader }) => {
  const [copied, setCopied] = useState(false);
  const { currentViewer, model, multiple, isPremium } = attributes;

  return (
    <InspectorControls>
      {postType === "bp3d-model-viewer" && (
        <PanelBody>
          <PanelRow>
            <div className="b3dviewer_front_shortcode justify-center">
              <ClipboardButton
                title={copied ? __("Copied", "model-viewer") : ""}
                variant="primary"
                text={`[3d_viewer id=${postId}]`}
                onCopy={() => setCopied(true)}
                onFinishCopy={() => setCopied(false)}
              >
                {__("Copy Shortcode", "model-viewer")}
              </ClipboardButton>
              {copied && <Snackbar>{__("Copied Succesfully", "model-viewer")}</Snackbar>}
            </div>
          </PanelRow>
        </PanelBody>
      )}
      <TabPanel
        className="b3dviewer-tab-panel bPlTabPanel"
        activeClass="active activeTab"
        tabs={[
          {
            name: "settings",
            title: __("Settings", "model-viewer"),
            className: "settings",
          },
          {
            name: "style",
            title: __("Style", "model-viewer"),
            className: "style",
          },
        ]}
      >
        {(tab) => {
          return (
            <span>
              {tab.name === "settings" && (
                <Panel>
                  <ModelForm attributes={attributes} setAttributes={setAttributes} />

                  {currentViewer === "modelViewer" && <>
                    {model.modelUrl && !multiple && isPremium && <>
                      <Lighting attributes={attributes} setAttributes={setAttributes} />

                      {/* Already added this feature in the ModelForm Component */}
                      {/* <AR isPremium={isPremium} value={model.ar} onChange={(ar) => setAttributes({model: {...model, ar}})} /> */}

                      <Texture modelReader={modelReader} viewerRef={viewerRef} attributes={attributes} setAttributes={setAttributes} />
                    </>}
                    <Options attributes={attributes} setAttributes={setAttributes} />
                  </>}

                  {currentViewer === "O3DViewer" && <O3DVOptions attributes={attributes} setAttributes={setAttributes} />}
                </Panel>
              )}
              {tab.name === "style" && (
                <Panel>
                  <Style attributes={attributes} setAttributes={setAttributes} />
                  <Additional attributes={attributes} setAttributes={setAttributes} />
                </Panel>
              )}
            </span>
          );
        }}
      </TabPanel>
    </InspectorControls>
  );
};

export default withSelect((select) => {
  const postType = select("core/editor")?.getCurrentPostType() || "product";
  const postId = select("core/editor")?.getCurrentPostId();

  return {
    postId,
    postType,
  };
})(Settings);
