import { SelectControl, ToggleControl, PanelBody, PanelRow, Tooltip } from "@wordpress/components";
import { useState } from "react";
import { __ } from "@wordpress/i18n";
import { produce } from "immer";
import { BtnGroup, InlineMediaUpload, ItemsPanel } from './../../../../../../../../bpl-tools/Components'

import { BControlPro } from "../../../../../../../../wp-utils/v1/components/";

const ModelForm = ({ attributes, setAttributes }) => {
  const { model, models, multiple, isPremium, currentViewer, O3DVSettings, AR = {} } = attributes;
  const [activeIndex, setActiveIndex] = useState(0);


  const modelViewer = ["gltf", "glb"].includes(model.ext || "glb");

  return (
    <PanelBody title={__("Model", "model-viewer")} initialOpen={false} className="bPlPanelBody">
      <BtnGroup value={currentViewer} label={__("3D Viewer", "model-viewer")} options={[
        { label: __("Support only .glb, .glTF", "model-viewer"), value: 'modelViewer', icon: 'Lite' },
        { label: __("Support .obj, .3ds, .stl, .ply, .gltf, .off, .3dm, .fbx, .dae, .wrl, .3mf, amf, ifc, .brep, .step, .iges, .fcstd, and .bim  file types", "model-viewer"), value: 'O3DViewer', icon: 'Advanced' }
      ]} onChange={(value) => setAttributes({ currentViewer: value })} isTextIcon={true}>
      </BtnGroup>

      <p className="bp3d_help components-base-control__help">
        <i>
          The Lite Viewer prioritizes .glb and .glTF files with robust features, while the Advanced Viewer, supporting{" "}
          <Tooltip delay={100} text="obj, .3ds, .stl, .ply, .gltf, .off, .3dm, .fbx, .dae, .wrl, .3mf, amf, ifc, .brep, .step, .iges, .fcstd, .bim">
            <strong>
              <u> almost every file types</u>
            </strong>
          </Tooltip>
          , has a more streamlined functionality.
        </i>
      </p>
      {/* {modelViewer && ( */}
      <PanelRow>
        <BControlPro label={__("Use Multiple Model?", "model-viewer")} isPremium={isPremium} Component={ToggleControl} checked={multiple} onChange={() => setAttributes({ multiple: !multiple })} />
      </PanelRow>
      {/* )} */}
      {multiple && isPremium ? (
        <>
          <ItemsPanel {...{ attributes, setAttributes, activeIndex, setActiveIndex, arrKey: 'models', ItemSettings, isPremium, design: 'sortable', newItem: {} }} />
        </>
      ) : (
        <>
          <InlineMediaUpload
            value={model?.modelUrl}
            placeholder={__("Model URL", "model-viewer")}
            onChange={(modelUrl) => {
              setAttributes({ model: { ...model, modelUrl, ext: modelUrl.split(".")?.[modelUrl.split(".").length - 1] }, O3DVSettings: { ...O3DVSettings, camera: null } });
            }}
            types={["model/gltf-binary", "model/obj", "application/octet-stream", "application/x-3ds", "application/vnd.ms-pki.stl", "text/vnd.in3d.3dml", "application/collada+xml", "model/vrml", "application/vnd.ms-3mfdocument"]}
            label={__("Model URL", "model-viewer")}
          />

          {currentViewer === "modelViewer" && (
            <>
              <InlineMediaUpload value={model?.poster} placeholder={__("Model Poster", "model-viewer")} onChange={(poster) => setAttributes({ model: { ...model, poster } })} types={["image"]} label={__("Model Poster", "model-viewer")} />

              <BControlPro Component={InlineMediaUpload} isPremium={isPremium} value={model?.skyboxImage} placeholder={__("HDR Image src", "model-viewer")} onChange={(skyboxImage) => setAttributes({ model: { ...model, skyboxImage } })} types={["image"]} label={__("HDR skybox image", "model-viewer")} />

              <PanelRow>
                <label>{__("Use Decoder", "model-viewer")}</label>
                <SelectControl
                  options={[
                    { label: "None", value: "none" },
                    { label: "Draco", value: "Draco" },
                  ]}
                  value={model?.decoder}
                  onChange={(decoder) => setAttributes({ model: { ...model, decoder } })}
                />
              </PanelRow>

              {model?.decoder === "Draco" && (
                <>
                  <InlineMediaUpload value={model?.binFile} placeholder={__("Bin File", "model-viewer")} onChange={(binFile) => setAttributes({ model: { ...model, binFile } })} types={["application/octet-stream"]} label={__("Bin File", "model-viewer")} />
                </>
              )}

              <BControlPro className="mt5" label={__("Enable AR", "model-viewer")} isPremium={isPremium} Component={ToggleControl} checked={model.arEnabled} onChange={() => setAttributes({ model: { ...model, arEnabled: !model.arEnabled } })} />

              {model.arEnabled && (
                <>
                  <InlineMediaUpload className="mt5" types={['model']} value={model?.modelISOSrc} placeholder={__("3D Model (iOS)", "model-viewer")} onChange={(modelISOSrc) => setAttributes({ model: { ...model, modelISOSrc } })} label={__("3D Model (iOS)", "model-viewer")} />

                  <SelectControl
                    className="mt5"
                    label={__("AR Placement", "model-viewer")}
                    options={[
                      { label: "Floor", value: "floor" },
                      { label: "Wall", value: "wall" },
                    ]}
                    value={model?.arPlacement}
                    onChange={(arPlacement) => setAttributes({ model: { ...model, arPlacement } })}
                  />

                  <SelectControl
                    className="mt5"
                    label={__("AR Mode", "model-viewer")}
                    options={[
                      { label: "webxr", value: "webxr" },
                      { label: "Scene Viewer", value: "scene-viewer" },
                      { label: "Quick Look", value: "quick-look" },
                    ]}
                    value={model?.arMode}
                    onChange={(arMode) => setAttributes({ model: { ...model, arMode } })}
                  />
                </>
              )}
            </>
          )}
        </>
      )}
    </PanelBody>
  );
};

export default ModelForm;


export const ItemSettings = ({ arrKey, attributes, setAttributes, isPremium, index }) => {
  const model = attributes[arrKey][index] || {};

  const { currentViewer } = attributes;

  const updateModels = (key, value) => {
    const newModels = produce(attributes[arrKey], (draft) => {
      draft[index][key] = value;
    });
    setAttributes({ models: newModels });
  };

  return <>
    <InlineMediaUpload value={model?.modelUrl} placeholder={__("Model URL", "model-viewer")} onChange={(modelUrl) => updateModels("modelUrl", modelUrl)} types={["model/gltf-binary"]} label={__("Model URL", "model-viewer")} />

    <InlineMediaUpload value={model?.poster} placeholder={__("Model Poster", "model-viewer")} onChange={(poster) => updateModels("poster", poster)} types={["image"]} label={__("Model Poster", "model-viewer")} />

    <BControlPro isPremium={isPremium} Component={InlineMediaUpload} value={model?.skyboxImage} placeholder={__("HDR skybox image", "model-viewer")} onChange={(skyboxImage) => updateModels("skyboxImage", skyboxImage)} types={["image"]} label={__("HDR skybox image", "model-viewer")} />

    {currentViewer === "modelViewer" && (
      <>
        <PanelRow>
          <label>{__("Use Decoder", "model-viewer")}</label>
          <SelectControl
            options={[
              { label: "None", value: "none" },
              { label: "Draco", value: "draco" },
            ]}
            value={model?.decoder}
            onChange={(decoder) => updateModels("decoder", decoder)}
          />
        </PanelRow>
        {model?.decoder === "draco" && (
          <>
            <InlineMediaUpload value={model?.binFile} placeholder={__("Bin File", "model-viewer")} onChange={(binFile) => updateModels("binFile", binFile)} types={["application/octet-stream"]} label={__("Bin File", "model-viewer")} />
          </>
        )}

        <BControlPro label={__("Enable AR", "model-viewer")} isPremium={isPremium} Component={ToggleControl} checked={model?.arEnabled} onChange={() => updateModels("arEnabled", !model.arEnabled, index)} />

        {model.arEnabled && <>
          <InlineMediaUpload types={['model']} value={model?.modelISOSrc} placeholder={__("3D Model (iOS)", "model-viewer")} onChange={(modelISOSrc) => updateModels("modelISOSrc", modelISOSrc, index)} label={__("3D Model (iOS)", "model-viewer")} />



          <SelectControl
            label={__("AR Placement", "model-viewer")}
            options={[
              { label: "Floor", value: "floor" },
              { label: "Wall", value: "wall" },
            ]}
            value={model?.arPlacement}
            onChange={(arPlacement) => updateModels("arPlacement", arPlacement, index)}
          />

          <SelectControl
            label={__("AR Mode", "model-viewer")}
            options={[
              { label: "webxr", value: "webxr" },
              { label: "Scene Viewer", value: "scene-viewer" },
              { label: "Quick Look", value: "quick-look" },
            ]}
            value={model?.arMode}
            onChange={(arMode) => updateModels("arMode", arMode, index)}
          />
        </>}

      </>
    )}
  </>
}