import React from "react";
import { PanelBody, ToggleControl, Button } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import { BColor, BControlPro } from "../../../../../../../../wp-utils/v1/components";

const O3DVOptions = ({ attributes, setAttributes }) => {
  const { O3DVSettings, multiple, isPremium } = attributes;
  const { isFullscreen, isNavigation = false, isPagination = false, mouseControl, zoom = true, showEdge = false, edgeColor = "#000000" } = O3DVSettings;

  const handleChange = (option) => {
    setAttributes({ O3DVSettings: { ...O3DVSettings, ...option } });
  };

  return (
    <PanelBody title={__("Options", "model-viewer")}>
      <ToggleControl label={__("Fullscreen", "model-viewer")} checked={isFullscreen} onChange={() => handleChange({ isFullscreen: !isFullscreen })} />

      <ToggleControl label={__("Mouse Control", "model-viewer")} checked={mouseControl} onChange={() => handleChange({ mouseControl: !mouseControl })} />

      <BControlPro Component={ToggleControl} isPremium={isPremium} label={__("Zoom", "model-viewer")} checked={zoom} onChange={() => handleChange({ zoom: !zoom })} />

      <BControlPro Component={ToggleControl} isPremium={isPremium} label={__("Show Edge", "model-viewer")} checked={showEdge} onChange={() => handleChange({ showEdge: !showEdge })} />

      {showEdge && <BColor label={__("Edge Color", "model-viewer")} value={edgeColor} onChange={(edgeColor) => handleChange({ edgeColor })} />}

      {(multiple || !isPremium) && (
        <>
          <BControlPro isPremium={isPremium} Component={ToggleControl} label={__("show thumbnails list", "model-viewer")} checked={isPagination} onChange={() => handleChange({ isPagination: !isPagination })} />

          <BControlPro isPremium={isPremium} Component={ToggleControl} label={__("show arrow", "model-viewer")} checked={isNavigation} onChange={() => handleChange({ isNavigation: !isNavigation })} />
        </>
      )}
    </PanelBody>
  );
};

export default O3DVOptions;
