import { __ } from '@wordpress/i18n';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Button, PanelRow, TextControl } from '@wordpress/components';
import { Fragment } from 'react';


import "./style.css";

function BMediaUpload({ value, type = [], onSelect, placeholder, label, onlyButton = false }) {

  const handleUpload = () => {
    const frame = wp.media({
      // title: 'Select or Upload Media Of Your Chosen Persuasion',
      button: {
        text: "Select",
      },
      multiple: false, // Set to true to allow multiple files to be selected
    });
    frame.open();
    frame.on("select", () => {
      var attachment = frame.state().get("selection").first().toJSON();
      onSelect(attachment.url);
    });
  };

  if (onlyButton) {
    return <MediaUploadCheck fallback={<Button icon="upload" className="button button-primary" onClick={handleUpload} />}>{<MediaUpload allowedTypes={type} onSelect={(file) => onSelect(file.url)} render={({ open }) => <Button className="button button-primary" onClick={open} icon={"upload"}></Button>} />}</MediaUploadCheck>
  }

  return (
    <div className="bMediaUpload">
      <Fragment>
        {label && <label>{label}</label>}
        <PanelRow className="width-100">
          <MediaUploadCheck fallback={<Button icon="upload" className="button button-primary" onClick={handleUpload} />}>{<MediaUpload allowedTypes={type} onSelect={(file) => onSelect(file.url)} render={({ open }) => <Button className="button button-primary" onClick={open} icon={"upload"}></Button>} />}</MediaUploadCheck>
          <TextControl value={value} onChange={(fileUrl) => onSelect(fileUrl)} placeholder={placeholder} />
        </PanelRow>
      </Fragment>
    </div>
  );
}

export default BMediaUpload;
