import { __ } from "@wordpress/i18n";
import { useState, useEffect } from "@wordpress/element";
import { PanelBody, PanelRow, ToggleControl, SelectControl, RangeControl, __experimentalNumberControl as NumberControl } from "@wordpress/components";
import { BControlPro } from "../../../../../../../../wp-utils/v1/components";

const Options = ({ attributes, setAttributes }) => {
  const {
    animation,
    autoRotate,
    autoplay,
    fullscreen,
    lazyLoad,
    loadingPercentage,
    model,
    multiple,
    models,
    mouseControl,
    progressBar,
    rotate,
    rotateAlongX,
    rotateAlongY,
    rotationPerSecond,
    selectedAnimation,
    shadow,
    uniqueId,
    variant,
    isPagination,
    isNavigation,
    isPremium,
    zoom,
    zoomLevel = 1,
  } = attributes;

  const [animations, setAnimations] = useState([]);

  useEffect(() => {
    setTimeout(() => {
      document.querySelector(`#${uniqueId} model-viewer`)?.addEventListener("load", function () {
        const animations = this.availableAnimations.map((animation) => {
          return { label: animation, value: animation };
        });
        setAnimations([...animations, { label: "None", value: "" }]);
      });
    }, 0);
  }, [uniqueId, model, models, multiple]);

  return (
    <>
      <PanelBody title={__("Options", "model-viewer")} className="bPlPanelBody">
        <ToggleControl label={__("Fullscreen", "model-viewer")} checked={fullscreen} onChange={() => setAttributes({ fullscreen: !fullscreen })} />

        {["gltf", "glb"].includes(model.ext || "glb") && (
          <>
            <ToggleControl className="mt5" label={__("Mouse Control", "model-viewer")} checked={mouseControl} onChange={() => setAttributes({ mouseControl: !mouseControl })} />

            <ToggleControl className="mt5" label={__("Lazy Load", "model-viewer")} checked={lazyLoad} onChange={() => setAttributes({ lazyLoad: !lazyLoad })} />

            <ToggleControl className="mt5" label={__("Show Loading Percentage", "model-viewer")} checked={loadingPercentage} onChange={() => setAttributes({ loadingPercentage: !loadingPercentage })} />

            <ToggleControl className="mt5" label={__("Show Progressbar", "model-viewer")} checked={progressBar} onChange={() => setAttributes({ progressBar: !progressBar })} />

            <BControlPro className="mt5" Component={ToggleControl} isPremium={isPremium} label={__("Enable Shadow", "model-viewer")} checked={shadow} onChange={() => setAttributes({ shadow: !shadow })} />

            <BControlPro className="mt5" isPremium={isPremium} label={__("Autoplay ( if animated )", "model-viewer")} Component={ToggleControl} checked={autoplay} onChange={() => setAttributes({ autoplay: !autoplay })} />

            <BControlPro className="mt5" isPremium={isPremium} label={__("Enable Variant Selector", "model-viewer")} Component={ToggleControl} checked={variant} onChange={() => setAttributes({ variant: !variant })} />

            <BControlPro className="mt5" isPremium={isPremium} label={__("Enable Animation Selector", "model-viewer")} Component={ToggleControl} checked={animation} onChange={() => setAttributes({ animation: !animation })} />

            {animation && animations.length !== 0 && !multiple && (
              <PanelRow>
                <label>{__("Set Animation", "model-viewer")}</label>
                <SelectControl options={animations} value={selectedAnimation} onChange={(selectedAnimation) => setAttributes({ selectedAnimation })} />
              </PanelRow>
            )}

            <BControlPro className="mt5" isPremium={isPremium} Component={ToggleControl} label={__("Auto Rotate", "model-viewer")} checked={autoRotate} onChange={() => setAttributes({ autoRotate: !autoRotate })} />

            {autoRotate && (
              <>
                <PanelRow>
                  <label>{__("Rotate Per Second (deg)", "model-viewer")}</label>
                  <NumberControl value={rotationPerSecond} onChange={(rotationPerSecond) => setAttributes({ rotationPerSecond })} />
                </PanelRow>
              </>
            )}

            {!multiple && (
              <>
                <BControlPro isPremium={isPremium} Component={ToggleControl} label={__("Rotate Direction", "model-viewer")} checked={rotate} onChange={() => setAttributes({ rotate: !rotate })} />

                {rotate && (
                  <>
                    <NumberControl labelPosition="side" label={__("Rotate Along X", "model-viewer")} onChange={(value) => setAttributes({ rotateAlongX: parseInt(value) })} value={rotateAlongX} max={360} min={0} />
                    <NumberControl labelPosition="side" label={__("Rotate Along Y", 'model-viewer')} onChange={(value) => setAttributes({ rotateAlongY: parseInt(value) })} value={rotateAlongY} max={360} min={0} />

                  </>
                )}
              </>
            )}

            <BControlPro isPremium={isPremium} className="mt5" Component={ToggleControl} label={__("Enable Zoom", "model-viewer")} checked={zoom} onChange={() => setAttributes({ zoom: !zoom })} />

            <BControlPro isPremium={isPremium} className="mt10" Component={RangeControl} labelPosition="side" label={__("Zoom Label", "model-viewer")} value={zoomLevel} onChange={(zoomLevel) => setAttributes({ zoomLevel })} min={0.5} step={0.1} max={10} />

            {(multiple || !isPremium) && (
              <>
                <BControlPro isPremium={isPremium} className="mt5" Component={ToggleControl} label={__("show thumbnails list", "model-viewer")} checked={isPagination} onChange={() => setAttributes({ isPagination: !isPagination })} />

                <BControlPro isPremium={isPremium} className="mt5" Component={ToggleControl} label={__("show arrow", "model-viewer")} checked={isNavigation} onChange={() => setAttributes({ isNavigation: !isNavigation })} />
              </>
            )}
          </>
        )}
      </PanelBody>
    </>
  );
};

export default Options;
